import { Injectable } from '@angular/core';

import { graphqlOperation } from 'aws-amplify';
import { listPageids } from '../../graphql/queries';
import { AmplifyService } from 'aws-amplify-angular';
import { Observable, from, of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SearchService {

  limit = 100;
  list;
  nextToken;

  constructor(
    private amplifyService: AmplifyService
  ) { }

  public getTest(): string {
    return 'test!@!!!';
  }

  public getData(): [] {
    return null;
  }

  public getListData(strFilter: string): Observable<any> {
    let inputFilter = {
      defName: {
        contains: strFilter
      }
    };

    if (strFilter === '') {
      inputFilter = null;
    }
    return from(this.amplifyService.api().graphql(graphqlOperation(listPageids, {filter: inputFilter, limit: 10000})));
  }

}
