import { Component, OnInit, Output, EventEmitter } from '@angular/core';

import { SearchService } from './search.service';
import { List } from '../list/list';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {

  serviceAreaList: string[] = [];
  typeList: string[] = [];

  @Output() searchListEvent = new EventEmitter();

  pageIdList: List[] = [];
  searchResultList: List[] = [];

  keyword = '';
  selectedAreaValue = '';
  selectedTypeValue = '';

  constructor(
    private searchService: SearchService
  ) {
  }

  ngOnInit() {
    this.searchService.getListData('').subscribe(dt => {
      this.pageIdList = dt.data.listPageids.items;

      const objA: object = {};
      const objB: object = {};
      this.pageIdList.forEach(item => {
        if (!objA[item.serviceArea]) {
          objA[item.serviceArea] = item.serviceArea;
          this.serviceAreaList.push(item.serviceArea);
        }

        if (!objB[item.defType]) {
          objB[item.defType] = item.defType;
          this.typeList.push(item.defType);
        }
      });

      this.search();
    }, error => {
      console.log('error : ', error);
    });
  }

  search() {
    this.searchResultList = this.pageIdList.filter(jo => {
      if ((jo.defId.toLowerCase().includes(this.keyword.toLowerCase())
          || jo.defName.toLowerCase().includes(this.keyword.toLowerCase()))
          && jo.defType.includes(this.selectedTypeValue)
          && jo.serviceArea.includes(this.selectedAreaValue)) {
        return true;
      }
    });

    this.searchListEvent.emit(
      (!this.keyword && !this.serviceAreaList.length && !this.typeList.length) ?
        this.pageIdList : this.searchResultList
    );
  }

  onChangeArea(areaValue) {
    this.selectedAreaValue = areaValue;
    this.search();
  }

  onChageType(typeValue) {
    this.selectedTypeValue = typeValue;
    this.search();
  }

  keydownEvent(event) {
    if (event.keyCode === 13) {
      this.search();
    }
  }
}
